import {
  SECONDS
} from './constants'

export const formatFilters = {
  filters: {
    formatDigits (value, digits = 4) {
      const power = Math.pow(10, digits)
      return (Math.round(value * power) / power).toFixed(digits)
    },
    toInt (value) {
      return Math.round(value)
    },
    dynamicTime (seconds) {
      // TODO (28.01.19) use i18n
      if (seconds <= SECONDS.minute) {
        return `${seconds} ${seconds > 1 ? 'Sekunden' : 'Sekunde'}`
      }
      else if (seconds <= SECONDS.hour) {
        const unit = Math.round(seconds / SECONDS.minute)
        return `${unit} ${unit > 1 ? 'Minuten' : 'Minute'}`
      }
      else if (seconds <= SECONDS.day) {
        const unit = Math.round(seconds / SECONDS.hour)
        return `${unit} ${unit > 1 ? 'Stunden' : 'Stunde'}`
      }
      else if (seconds <= SECONDS.week) {
        const unit = Math.round(seconds / SECONDS.day)
        return `${unit} ${unit > 1 ? 'Tage' : 'Tag'}`
      }
      else if (seconds <= SECONDS.month) {
        const unit = Math.round(seconds / SECONDS.week)
        return `${unit} ${unit > 1 ? 'Wochen' : 'Woche'}`
      }
      else if (seconds <= SECONDS.year) {
        const unit = Math.round(seconds / SECONDS.month)
        return `${unit} ${unit > 1 ? 'Monate' : 'Monat'}`
      }
      else if (seconds >= SECONDS.year) {
        const unit = Math.round(seconds / SECONDS.year)
        return `${unit} ${unit > 1 ? 'Jahre' : 'Jahr'}`
      }
    }
  }
}
